/* -- Structure -- */
#wrapper {
	width: 90%;
	margin:  2em auto;
	position: relative;
}

/* -- Layout -- */
#container {
	float: right;
	width: 72%;
	min-height: 444px;
}
#header,
#sidebar{
	width: 25%;
	float: left;
}
#footer {
	clear: both;
}





/* -- Global Elements -- */
body { background: #eee; }

/* links */


a:link,
a:visited{
	color: #1e73be;
	text-decoration: none;
	-webkit-transition: color 0.3s ease-in;
	-moz-transition: color 0.3s ease-in;
	-o-transition: color 0.3s ease-in;
	transition: color 0.3s ease-in;
}
a:active,
a:hover{
	color: #444;
}

.entry-content a,
.widget-area a {
  position: relative;
  text-decoration: none;
}
.entry-content a:before,
.widget-area a:before, 
.entry-content a:after,
.widget-area a:after {
  content: "";
  position: absolute;
  bottom: 2px;
  left: 0;
  right: 0;
  height: 2px;
  background-color: #444;
}
.entry-content a:before,
.widget-area a:before {
  opacity: 0;
  -webkit-transform: translateY(-8px);
          transform: translateY(-8px);
  -webkit-transition: opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s;
  transition: transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s, -webkit-transform 0s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.entry-content a:after,
.widget-area a:after {
  opacity: 0;
  -webkit-transform: translateY(4px);
          transform: translateY(4px);
  -webkit-transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.entry-content a:hover:before, 
.entry-content a:hover:after, 
.entry-content a:focus:before, 
.entry-content a:focus:after,
.widget-area a:hover:before, 
.widget-area a:hover:after, 
.widget-area a:focus:before, 
.widget-area a:focus:after {
  opacity: 1;
  -webkit-transform: translateY(0);
          transform: translateY(0);
}
.entry-content a:hover:before, 
.entry-content a:focus:before,
.widget-area a:hover:before, 
.widget-area a:focus:before
 {
  -webkit-transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s;
  transition: transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0.2s, -webkit-transform 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}
.entry-content a:hover:after, 
.entry-content a:focus:after,
.widget-area a:hover:after, 
.widget-area a:focus:after {
  -webkit-transition: opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  transition: transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s 0.2s;
  transition: transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275), opacity 0s 0.2s, -webkit-transform 0s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

/* thanks to https://codepen.io/devilishalchemist/pen/ZYLZay for hover effects */




hr {
	background-color: #d5d5d5;
	background-color: rgba(0,0,0,0.2);
	border: 0;
	clear: both;
	height: 1px;
	margin-bottom: 1.6em;
}


/* table styles */

#content table {
	border: 1px solid #e7e7e7;
	margin: 0 -1px 24px 0;
	text-align: left;
	width: 100%;
}
#content tr th,
#content thead th {
	color: #888;
	font-size: 12px;
	font-weight: bold;
	line-height: 18px;
	padding: 9px 24px;
}
#content tr td {
	border-top: 1px solid #e7e7e7;
	padding: 6px 24px;
}
#content tr.odd td {
	background: #f2f7fc;
}


/* -- Header -- */
#header {
	margin: 0 0 .5em 0;
	border-bottom: 1px solid #d5d5d5;
	border-bottom: 1px solid rgba(0,0,0,0.2);
}

h1#site-title {
	font-family: 'spectral', serif;
	font-size: 2.2em;
}

#site-title a {
	text-decoration: none;
	display: block;
}

#site-description {
	font-style: italic;
	font-size: 1.2em;
	margin: 0 0 .5em 0;
}


/* -- Content -- */
#main {
	clear: both;
	overflow: hidden;
	padding: 0 0 1em 0;
}

#content {
	// font-size: 1.1em;
}

/* -- Post -- */
.post {
	margin-bottom: 1.5em;
	padding-bottom: 1.5em;
	border-bottom: 1px solid #d5d5d5;
	border-bottom: 1px solid rgba(0,0,0,0.2);
}

.home .sticky {
	background: #fff;
	padding: 2em;
	border-bottom: none !important;
}

.single .post {
	padding-bottom: 0;
	border-bottom: none;
}

/* entry title */
.entry-title {
	color: #444;
	margin-top: .25em;
}



/* entry meta */
.entry-meta {
	color: #777;
	font-size: .75em;
}
.entry-meta abbr,
.entry-utility abbr {
	border: none;
}
.entry-meta abbr:hover,
.entry-utility abbr:hover {
	border-bottom: 1px dotted #666;
}

/* content / summary */
.entry-content,
.entry-summary {
	clear: both;
	padding: 1em 0 0 0;
	overflow: hidden;
	margin-bottom: .5em;
}
#content .entry-summary p:last-child {
	margin-bottom: 1em;
}
.entry-content fieldset {
	border: 1px solid #e7e7e7;
	margin: 0 0 1.8em 0;
	padding: 1.8em;
}
.entry-content fieldset legend {
	background: #fff;
	color: #000;
	font-weight: bold;
	padding: 0 1.8em;
}
.entry-content input {
	margin: 0 0 1em 0;
}
.entry-content input.file,
.entry-content input.button {
	margin-right: 1em;
}
.entry-content label {
	color: #888;
	font-size: 1em;
}
.entry-content select {
	margin: 0 0 1em 0;
}
.entry-content sup,
.entry-content sub {
	font-size: .9em;
}
.entry-content blockquote.left {
	float: left;
	margin-left: 0;
	margin-right: 1em;
	text-align: right;
	width: 33%;
}
.entry-content blockquote.right {
	float: right;
	margin-left: 1em;
	margin-right: 0;
	text-align: left;
	width: 33%;
}
.page-link {
	clear: both;
	color: #000;
	font-weight: bold;
	margin: 0 0 2em 0;
	word-spacing: 0.5em;
}
.page-link a:link,
.page-link a:visited {
	background: #f1f1f1;
	color: #333;
	font-weight: normal;
	padding: 0.5em 0.75em;
	text-decoration: none;
	-webkit-transition: all 0.3s ease-in;
	-moz-transition: all 0.3s ease-in;
	-o-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
}
.home .sticky .page-link a {
	background: #999;
	background: rgba(0,0,0,0.1);
}

.home .sticky .page-link a:hover {
	color: #fff;
	background: rgba(0,0,0,0.4);
}

.page-link a:active,
.page-link a:hover {
	color: #000;
}
body.page .edit-link {
	clear: both;
	display: block;
}

/* author */
#entry-author-info {
	background: #d5d5d5;
	background: rgba(0,0,0,0.1);
	clear: both;
	margin: 1em 0 2em 0;
	overflow: hidden;
	padding: 1.5em;
}
#entry-author-info #author-avatar {
	float: left;
	height: auto;
	width: 20%;
	margin: 0;
}
#entry-author-info #author-avatar img{
	width: 100%;
}

#entry-author-info #author-description {
	float: right;
	width: 75%;
}
#entry-author-info h2 {
	font-size: 1.2em;
}
#author-link a{
	text-decoration: none;
}
#author-link a:hover{
	text-decoration: underline;
}

/* utility */
.entry-utility {
	clear: both;
	color: #888;
	font-size: .8em;
}
.entry-meta a,
.entry-utility a {
	color: #888;
}
.entry-meta a:hover,
.entry-utility a:hover {
	color: #000;
}
#content .video-player {
	padding: 0;
}

/* continue reading link */
a.button,
a.more-link{
	text-decoration: none;
	padding: 10px 20px;
	color: #000;
	background: #d5d5d5;
	background: rgba(0,0,0,0.1);
	-webkit-transition: all 0.3s ease-in;
	-moz-transition: all 0.3s ease-in;
	-o-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
}
a.button:hover,
a.more-link:hover{
	color: #fff;
	background: #bdbdbd;
	background: rgba(0,0,0,0.2);
}

/* pagination */
#pagination{
	font-size: .8125em;
	line-height: 2.5em;
}
#pagination .page-numbers{
	border: 1px solid #a6a6a6;
	padding: 5px;
}
#pagination a:link{
	text-decoration: none;
}
#pagination a:active,
#pagination a:visited,
#pagination a:hover{
	text-decoration: underline;
}

/* -- Page -- */
.page-title {
	margin-bottom: .5em;
	padding-bottom: .5em;
	border-bottom: 1px solid #777;
	border-bottom: 1px solid rgba(0,0,0,0.2);
}

/* -- Gallery -- */
.category-gallery .size-thumbnail img {
	margin-bottom: 0;
}


/* -- Attachment pages -- */
.attachment .entry-content .entry-caption {
	font-size: 120%;
	margin-top: 1em;
	text-align: center;
}
.attachment .entry-content .nav-previous a:before {
	content: '\2190\00a0';
}
.attachment .entry-content .nav-next a:after {
	content: '\00a0\2192';
}

/* page navigation */
#nav-below {
	padding: 10px;
	font-size: .9em;
	overflow: hidden;
	background: rgba(0,0,0,0.1);
	border-top: 1px solid #d5d5d5;
	border-top: 1px solid rgba(0,0,0,0.2);
	border-bottom: 1px solid #d5d5d5;
	border-bottom: 1px solid rgba(0,0,0,0.2);
}
.nav-previous {
	float: left;
	width: 50%;
}
.nav-next {
	float: right;
	text-align: right;
	width: 50%;
}

/* -- Images -- */
img {
	margin: 0;
	height: auto;
	max-width: 100%;
}

.category-gallery .gallery-thumb,
.featured-image-blog{
	margin-bottom: .5em;
}
#content .featured-image-single{
	margin-bottom: .5em;

}

#content .attachment img {
	max-width: 900px;
}
#content .alignleft,
#content img.alignleft {
	display: inline;
	float: left;
	margin-right: 1em;
	margin-top: 4px;

}
#content .alignright,
#content img.alignright {
	display: inline;
	float: right;
	margin-left: 1em;
	margin-top: 4px;

}
#content .aligncenter,
#content img.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;

}
#content img.alignleft,
#content img.alignright,
#content img.aligncenter {
	margin-bottom: 12px;
}
#content .wp-caption {
	background: #f1f1f1;
	line-height: 18px;
	margin-bottom: 20px;
	padding: 4px;
	text-align: center;
}
#content .wp-caption img {
	margin: 5px 5px 0;
}
#content .wp-caption p.wp-caption-text {
	color: #888;
	font-size: 12px;
	margin: 5px;
}
#content .wp-smiley {
	margin: 0;
}
#content .gallery {
	margin: 0 auto 18px;
}
#content .gallery .gallery-item {
	float: left;
	margin: 0 .5%;
	text-align: center;
	width: 32%;
}
#content .gallery img {
	width: 100%;
}
#content .gallery .gallery-caption {
	color: #888;
	font-size: .8em;
	margin: 0 0 1em;
}
#content .gallery dl {
	margin: 0;
}
#content .gallery img {

}
#content .gallery br+br {
	display: none;
}
#content .attachment img { /* single attachment images should be centered */
	display: block;
	margin: 0 auto;
}


/* -- Comments -- */
#comments {
	clear: both;
	border-top: 1px solid #d5d5d5;
	border-top: 1px solid rgba(0,0,0,0.2);
}
#comments .navigation {
	padding: 0 0 18px 0;
}
h3#comments-title,
h3#reply-title {
	color: #444;
	font-size: 20px;
	font-weight: bold;
	margin-bottom: 0;
}
h3#comments-title {
	padding: 1em 0;
}
.commentlist {
	list-style: none;
	margin: 0;
}

.commentlist li.comment {
	border-bottom: 1px solid #a6a6a6;
	margin: 0 0 1em 0;
	padding: 0;
	position: relative;
	overflow: hidden;
}

.commentlist li:last-child {
	border-bottom: none;
	margin-bottom: 0;
}
#comments .comment-body ul,
#comments .comment-body ol {
	margin-bottom: 18px;
}
#comments .comment-body p:last-child {
	margin-bottom: 6px;
}
#comments .comment-body blockquote p:last-child {
	margin-bottom: 1em;
}
.commentlist ol {
	list-style: decimal;
}

.commentlist .avatar {
	width: 100%;
	height: auto;
}

.comment-author-image {
	overflow: hidden;
	float: left;
	width: 10%;
	margin-right: 2em;
}

.comment-content {
	overflow: hidden;
}


.comment-author cite {
	color: #000;
	font-style: normal;
	font-weight: bold;
}
.comment-author .says {
	font-style: italic;
}
.comment-meta {
	font-size: 12px;
	margin: 0 0 18px 0;
}
.comment-meta a:link,
.comment-meta a:visited {
	color: #888;
	text-decoration: none;
}
.comment-meta a:active,
.comment-meta a:hover {
	color: #5485e4;
}
.commentlist .even {
}
.commentlist .bypostauthor {
}
.reply {
	font-size: 12px;
	padding: 0 0 1em 0;
}
.reply a,
a.comment-edit-link {
	color: #888;
}
.reply a:hover,
a.comment-edit-link:hover {
	color: #5485e4;
}
.commentlist .children {
	list-style: none;
	margin: 0;
}

.commentlist .children li {
	display: block;
	border: none;
	margin: 0;
}

/* reply's by post author */
.bypostauthor {
	background: #fff;
	overflow: hidden;
	padding: .5em !important;
	margin-bottom: 1em !important;	
}

.bypostauthor .children {
	background: #eee;
	padding: .5em 0 .5em .5em !important;
}


.nopassword,
.nocomments {
	display: none;
}
#comments .pingback {
	border-bottom: 1px solid #e7e7e7;
	margin-bottom: 18px;
	padding-bottom: 18px;
}
.commentlist li.comment+li.pingback {
	margin-top: -6px;
}
#comments .pingback p {
	color: #888;
	display: block;
	font-size: 12px;
	line-height: 18px;
	margin: 0;
}
#comments .pingback .url {
	font-size: 13px;
	font-style: italic;
}

/* Comments form */
input[type=submit] {
	color: #333;
}
#respond {
	margin: 1em 0;
	overflow: hidden;
	position: relative;
}
#respond p {
	margin: 0;
}
#respond .comment-notes {
	margin-bottom: 1em;
}

.children #respond {
	margin: 0 48px 0 0;
}
h3#reply-title {
	margin: 18px 0;
}
#comments-list #respond {
	margin: 0 0 18px 0;
}
#comments-list ul #respond {
	margin: 0;
}
#cancel-comment-reply-link {
	font-size: 12px;
	font-weight: normal;
	line-height: 18px;
}
#respond .required {
	color: #5485e4;
	font-weight: bold;
}
#respond label {
	color: #888;
	font-size: 12px;
}
#respond input {
	margin: 0 0 9px;
	width: 98%;
}
#respond textarea {
	width: 98%;
}
#respond .form-submit {
	margin: 12px 0;
}
#respond .form-submit input {
	font-size: 14px;
	width: auto;
	color: #fff;
	padding: 5px 10px;
	background: #7b7a79;
	border: 1px solid #777;
	border-radius: 3px;
}
#respond .form-submit input:hover{
	background: #7b7a79 url(images/gloss.png) -5px repeat-x;
}
#respond .form-submit input:active{
	background: #444;
}

/* remove form-allowed-tags */
.form-allowed-tags{
	display: none;
}

/*-- Widget Areas -- */
.widget-area ul {
	list-style: none;
	margin-left: 0;
}
.widget-area ul ul {
	list-style: square;
	margin-left: 1.3em;
}
.widget_search #s {/* This keeps the search inputs in line */
	width: 60%;
}
.widget_search label {
	display: none;
}
.widget-container {
	margin: 0 0 2em 0;
}

/* -- sidebar menu -- */
.widget-area .menu a {
	font-size: 1.2em;
}

.widget-area .menu a:hover,
.widget-area .menu .current-menu-item a {
	/*color: #fff;
	background: #8e8e8e;
	background: rgba(0,0,0,0.4);*/
}

.widget-area .entry-meta {
	font-size: 11px;
}
#wp_tag_cloud div {
	line-height: 1.6em;
}
#wp-calendar {
	width: 100%;
}
#wp-calendar caption {
	color: #222;
	font-size: 1em;
	font-weight: bold;
	padding-bottom: 4px;
	text-align: left;
}
#wp-calendar thead {
	font-size: 11px;
}
#wp-calendar thead th {
}
#wp-calendar tbody {
	color: #aaa;
}
#wp-calendar tbody td {
	background: #f5f5f5;
	border: 1px solid #fff;
	padding: 3px 0 2px;
	text-align: center;
}
#wp-calendar tbody .pad {
	background: none;
}
#wp-calendar tfoot #next {
	text-align: right;
}
.widget_rss a.rsswidget {
	color: #000;
}
.widget_rss a.rsswidget:hover {
	color: #5485e4;
}
.widget_rss .widget-title img {
	width: 11px;
	height: 11px;
}

/* Main sidebars */
#main .widget-area ul {
	margin: 0;
	padding: 0;
}
#main .widget-area ul ul {
	margin: 0;
	padding: 0;
	list-style-type: none;
}
#main ul.sub-menu,
#main ul.children {
	margin: 0 0 0 10px !important;
}

/* -- Contact Form 7 & Submit button styling - http://wordpress.org/extend/plugins/contact-form-7/ -- */
.wpcf7 input[type="text"],
.wpcf7 input[type="email"],
.wpcf7 textarea,
#respond input[type="text"],
#respond textarea,
#respond input[type="email"]{
	padding: .5em;
	border: 1px solid #ccc;
	color: #777 !important;
	font-size: 1em !important;
	line-height: 1.1em;
	background: #fff !important;
}
.wpcf7 input[type="text"],
.wpcf7 input[type="email"]{
	width: 60%;
}
#respond input[type="text"],
#respond input[type="email"]{
	width: 60%;
	display: block;
}
.wpcf7 textarea,
#respond textarea {
	padding: .5em;
	width: 80%;
	display: block;
}


/* -- submit buttons -- */
.wpcf7 input[type="submit"],
#respond input[type="submit"]{
	background: none;
	-webkit-transition: all 0.3s ease-in;
	-moz-transition: all 0.3s ease-in;
	-o-transition: all 0.3s ease-in;
	transition: all 0.3s ease-in;
	-webkit-appearance: none !important;
	color: #000;
	background: #d5d5d5;
	background: rgba(0,0,0,0.1);
	border: none;
	font-size: 18px;
	padding: 10px 20px;
	border-radius: 0;
	text-shadow: none;
	border: none;
}
.wpcf7 input[type="submit"]:hover,
#respond input[type="submit"]:hover{
	color: #fff;
	background: #333;
	background: rgba(0,0,0,0.4);
	-webkit-transition: all 0.25s ease-in;
	-moz-transition: all 0.25s ease-in;
	-o-transition: all 0.25s ease-in;
	transition: all 0.25s ease-in;
}
.wpcf7 input[type="submit"]:active,
#respond input[type="submit"]:active{
	background: #000;
}

/* -- CF7 Error Message Styles -- */
.wpcf7-response-output, 
.wpcf7-display-none,
.wpcf7-validation-errors,
.wpcf7-mail-sent-ng,
.wpcf7-not-valid-tip{
	background: #fff;
	box-shadow: 0 3px 6px #000;
	box-shadow: 0 3px 6px rgba(0,0,0,0.3);
	border-width: 1px !important;
}
.wpcf7-not-valid-tip{
	left: 2% !important;
	width: 98% !important;
}

/* -- Footer -- */
#site-info {
	float: right;
	text-align: right;
	font-size: .8em;
	font-weight: bold;
	width: 40%;
}
#footer {
	border-top: 1px solid #d5d5d5;
	border-top: 1px solid rgba(0,0,0,0.2);	
	margin-bottom: 2em;
}
#colophon {
	overflow: hidden;
	padding: 1em 0;
}
#site-info {
	font-weight: bold;
}
#site-info a {
	color: #444;
	text-decoration: none;
}

/* -- Print Styles -- */
@media print {
	body {
		background: none !important;
	}
	#wrapper {
		clear: both !important;
		display: block !important;
		float: none !important;
		position: relative !important;
	}
	#header {
		border-bottom: 2pt solid #000;
		padding-bottom: 18pt;
	}
	#colophon {
		border-top: 2pt solid #000;
	}
	#site-title,
	#site-description {
		float: none;
		line-height: 1.4em;
		margin: 0;
		padding: 0;
	}
	#site-title {
		font-size: 13pt;
	}
	.entry-content {
		font-size: 14pt;
		line-height: 1.6em;
	}
	.entry-title {
		font-size: 21pt;
	}

	#header img,
	#respond,
	.comment-edit-link,
	.edit-link,
	.navigation,
	.page-link,
	.widget-area {
		display: none !important;
	}
	#container,
	#header,
	#footer {
		margin: 0;
		width: 100%;
	}
	#content,
	.one-column #content {
		margin: 24pt 0 0;
		width: 100%;
	}
	.wp-caption p {
		font-size: 11pt;
	}
	#site-info {
		float: none;
		width: auto;
	}
	#colophon {
		width: auto;
	}
	img#wpstats {
		display: none;
	}

	#entry-author-info {
		border: 1px solid #e7e7e7;
	}
	#main {
		display: inline;
	}
	.home .sticky {
		border: none;
	}
}

/* hide mobile menu by default */
.selectnav,
.mobile-menu { 
	display: none;
}

/* from mobile up to tablet portrait view */
@media only screen and (max-width: 799px) {

	/* hide sidebar menu */
	.menu-header .menu,
	#sidebar-menu .widget-title {
	 	display:none;
	}
	
	/* show select menu	*/
	.js .selectnav { 
		display: block; 
		margin: 20px auto;
		width: 80%;	
	}
	
	#header {
		margin-bottom: 1.5em;
	}
	
	/* main containers full width */
	#container,
	#header,
	#sidebar {
		width: 100%;
		float: none;
	}
	
	/* more link full width */
	a.button,
	a.more-link {
		width: 100%;
		text-align: center;
		display: block;
	} 
	
	/* forms full width */
	input[type="text"],
	.wpcf7 input[type="text"],
	textarea,
	.wpcf7 textarea{
		width: 100%;
	}

	/* webkit tweak */
	body {
		-webkit-text-size-adjust: 111%;
	}
}



